@keyframes flip {
  0%,80% {
    transform: rotateY(360deg) 
  }
}
.menubar_wrapper {
  position: sticky;
  top: 0;
  padding-top: 10px;
  z-index: 100;
}
.mw_mob {
  position: fixed;
  bottom: 0;
  right: 0;
  top: auto;
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  width: 100%;
  padding: 0;
  justify-content: space-evenly;
}
.--active {
  transition: background-color 1s ease-in-out;
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(1.5px);
  // background-color: #f5f5f5;
}
.menubar_container {
  display: flex;
  flex-direction: row;
  background-color: transparent;
  will-change: background-color;
  justify-content: center;
  align-items: center;
  padding: 5px;

  &__mob{
    @extend .menubar_container;
    // border-radius: 15px 30px 45px;
    // border: 6px solid #ccc;
  }

  .menubar_option {
    margin-right: 80px;
    font-size: 15px;
    letter-spacing: 2.5px;
    text-transform: uppercase;
    display: inline-block;
    font-weight: 700;
    text-decoration: none;
    color: rgba(34, 34, 34, 0.7);
    
    & span {
      position: relative;
      display: inline-block;
      font-size: 30px;
      color: rgba(34, 34, 34, 0.7);
      text-transform: uppercase;
      animation: flip 6s infinite;
      animation-delay: calc(.2s * var(--i))
    }

    &:first-of-type{
      display: flex;
      flex-direction: row;
      font-size: 18px;
      padding-right: 11%;
    }
  }

.mo_lap{
  @extend .menubar_option;
  margin-right: 30px;
  &:first-of-type{
 
    padding-right: 6%;
  }
}

  .mo_mob {
    margin-right: 18px;
    font-size: 14px;
    letter-spacing: 2.2px;
    font-weight: 700;
    padding: 11px !important;
    font-size: 30px !important;
    color: #e89f71;
    background: transparent !important;

    &:first-of-type{
      display: block;
    }
  }
}

